import React, { useState } from 'react';
import axios from 'axios';
import { Link, navigate } from '@reach/router';

const NewProject = () => {

    const [name, setName] = useState("");
    const [dueDate, setDueDate] = useState("");
    const [errors, setErrors] = useState({});

    const handleSubmit = e => {
        e.preventDefault()
        const newProject = {name, dueDate}
        axios.post("http://localhost:8000/api/projects/new", newProject)
            .then(res => {
                console.log(res);
                if(res.data.errors){
                    setErrors(res.data.errors)
                }else{
                    navigate('/');
                }
            }).catch(err =>{
                console.log(err);
            });
    }
    
    return (
        <div className="row"> 
            <div className="col-sm-8 offset-sm-2">
                <Link to="/" className="btn btn-outline-dark mb-3">Back</Link>
                <div className="card">
                    <h5 className="card-header bg-dark text-light">Plan a new project</h5>
                    <div className="card-body">
                        <form onSubmit={handleSubmit}>
                            <div className="form-group">
                                <label>Project:</label>
                                <input type="text" className="form-control" name="name" value={name} onChange={e => setName(e.target.value)} />
                                <p className="text-danger">{errors.name ? errors.name.message: ''}</p>
                            </div>
                            <div className="form-group">
                                <label>Due Date:</label>
                                <input className="form-control" type="date" value={dueDate} onChange={e => setDueDate(e.target.value)} />                                
                                <p className="text-danger">{errors.dueDate ? errors.dueDate.message: ''}</p>
                            </div>
                            <button type="submit" className="btn btn-block btn-info my-4">Plan Project</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default NewProject;