import React, { useState, useEffect } from 'react';
import { Link } from '@reach/router';
import axios from 'axios';
import moment from 'moment';

const AllProjects = (props) => {

    const [allProjects, setAllProjects] = useState([]);

    useEffect( () => {
        getAllProjects();
    }, []);

    const getAllProjects = () => { 
        axios.get("http://localhost:8000/api/projects")
        .then(res => {
            console.log(res)
            setAllProjects(res.data.projects)
        })
        .catch(err => console.log(err))
    }

    const updateStatus = (e, project, newStatus) => {
        e.preventDefault()
        axios.put(`http://localhost:8000/api/projects/${project._id}/${newStatus}`)
        .then(res => {
            console.log(res);
            getAllProjects();
        })
        .catch(err => console.log(err))
    }

    const remove = (id) => { 
        axios.delete(`http://localhost:8000/api/projects/${id}`)
            .then(res => {
                console.log(res);
                getAllProjects();
            }).catch(err => console.error(err));
    }

    return (
        <div className="container">
            <Link to='/projects/new' className="btn btn-outline-dark mb-3">Add New Project</Link>
            <div className="row"> 

                <div className="col-sm-4">
                    <div className="card"> 
                        <h5 className="card-header bg-info text-light"> Backlog</h5>
                        <div className="card-body"> 
                        {allProjects.filter(p => p.status === "Backlog").map( (project, i) => 
                            <div className="card p-2" key={i}> 
                                <p className="font-weight-bold"> {project.name} </p>
                                <p> <span className="font-weight-bold"> Due Date: </span> {moment(project.dueDate).format("L")} </p>
                                <button type="button" className="btn btn-block btn-warning text-light font-weight-bold" onClick={ (e) =>  updateStatus(e, project, "InProgress") }>Start Project </button>
                            </div>
                        )}
                        </div>
                    </div>
                </div>

                <div className="col-sm-4">
                    <div className="card"> 
                        <h5 className="card-header bg-warning text-light"> In progress</h5>
                        <div className="card-body"> 
                        {allProjects.filter(p => p.status === "InProgress").map( (project, i) => 
                            <div className="card p-2" key={i}> 
                                <p className="font-weight-bold"> {project.name} </p>
                                <p> <span className="font-weight-bold"> Due Date: </span> {moment(project.dueDate).format("L")} </p>
                                <button type="button" className="btn btn-block btn-success text-light font-weight-bold" onClick={(e) =>  updateStatus(e, project, "Completed")}> Move to Completed </button>
                            </div>
                        )}
                        </div>
                    </div>
                </div>

                <div className="col-sm-4">
                    <div className="card"> 
                        <h5 className="card-header bg-success text-light"> Completed</h5>
                        <div className="card-body"> 
                        {allProjects.filter(p => p.status === "Completed").map( (project, i) => 
                            <div className="card p-2" key={i}> 
                                <p className="font-weight-bold"> {project.name} </p>
                                <p> <span className="font-weight-bold"> Due Date: </span> {moment(project.dueDate).format("L")} </p>
                                <button type="button" className="btn btn-block btn-danger font-weight-bold" onClick={e => remove(project._id)}>Remove Project</button>
                            </div>
                        )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AllProjects;